<template>
    <div class="Indetail">
      <div class="liucheng" v-if="detail.type!==3">
          <li v-for="(item,index) in liuList" :key="index">
            <div class="liuTime">
              <div :class="item.date?'lian':''">
                <div class="xian"></div>
                <div class="xian"></div>
              </div>
              <div :class="item.date?'timeDetail':''">
                  {{item.date}}
              </div>
            </div>
            <div class="status">
              <div class="lian">
                <div class="xian"></div>
                <div class="dian"  :class="item.date?'dianbig':''"></div>
                <div class="xian"></div>
              </div>
              <div class="statusDetail" :class="item.date?'statusDetails':''">
                {{item.status}}
              </div>
            </div>
          </li>
        <div class="imginfo">
          <img src="../assets/img/shouyixiangqing--renwu.png" alt="">
        </div>
      </div>
      <div class="IncomeDetail">
         <div class="jiner">
           <span class="info">收益金额</span><div><span>{{detail.money|moneyType}}</span>元</div>
         </div>
        <div class="type">
          <span class="info">收益类别</span>
          <div>
            <span v-if="detail.type==1">首佣</span>
            <span v-if="detail.type==2">续佣</span>
            <span v-if="detail.type==3">增员奖</span>
            <span v-if="detail.type==4">管理奖</span>
          </div>
        </div>
        <div class="time" v-if="detail.type!==3">
          <span class="info" >时间</span><div><span>{{detail.qian_time}}</span></div>
        </div>
        <div class="detail">
          <span class="info">详情</span><div>
            <span v-if="detail.type===3">贡献者：{{detail.who}}</span>
            <span v-else>
              <div v-if="detail.yong">佣金：{{detail.yong|moneyType}}</div>
              <div v-if="detail.jiang">奖金：{{detail.jiang|moneyType}}</div>
              <div v-if="detail.salesman">客户:{{detail.salesman.nickname}}</div>
              <div v-if="detail.insurance_name">保险名称:{{detail.insurance_name.title}}</div>
            </span>
          </div>
        </div>
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script>
import { apiGet, apiPost } from '../common/js/api';
    export default {
      name: "Income_detail",
      data(){
        const query = this.$route.query;
        return{
          incomeId:query.incomeId,
          process:'',
          liuList:[

            {
              'date':"2018-8-22 14:00",
              "status":"预约成功"
            },
            {
              'date':"2018-8-22 14:00",
              "status":"已见面"
            },
            {
              'date':"",
              "status":"已签约"
            },
            {
              'date':"",
              "status":"已返款"
            }
          ],
          detail:{
            money:5000,
            typenName:"管理奖",
            date:"2018-08-23    10:00:00",
            info:"被他如何让他被他如何让他进入天津体育课被他如何让他进入天津体育课被他如何让他进入天津体育课进入天津体育课"
          }
        }
      },
      created(){
        this.getDetail()
      },
      methods:{
        //获取收益详情
        getDetail(){
          let formData = new FormData();
          formData.append("id",this.incomeId)
           formData.append("accessToken",this.$cookie.get('accessToken'))
          // formData.append("accessToken",'3HzxiZdbTu1dcWkJN96N_TULIae8DIZ_')
          
          apiGet("/user/income-detail",{
            //   "accessToken":'3HzxiZdbTu1dcWkJN96N_TULIae8DIZ_',
            "accessToken":this.$cookie.get('accessToken'),
            id:this.incomeId
          }).then(res=>{
            console.log(res)
            const data = res.data.data;
            this.detail = res.data.data;
            this.process=data.process;

            // this.detail = res.data.detail

          })
        }
      }
    }
</script>

<style scoped lang="scss">
  .icon-kefu{
    top:0.4rem;
    right:0.2rem;
  }
  .Indetail{
      height: 100%;
    background: #E6E9ED;
      overflow: auto;
     padding-top: 0.18rem;
    .liucheng{
      width:3.5rem;
      min-height:2.42rem;
      padding-bottom: 0.2rem;
      background:linear-gradient(-90deg,rgba(127,173,249,1),rgba(70,133,238,1));
      border-radius:0.04rem;
      color: white;
      margin: 0 auto;
      position: relative;
      padding-top: 0.22rem;
      .imginfo{
        position: absolute;
        width: 0.86rem;
        height: 0.76rem;
        right: 0.63rem;
        bottom:0.55rem;
        img{
          width: 0.86rem;
          height: 0.76rem;
        }
      }
      li{margin-left: 0.28rem;
        .liuTime,.status{
          display: flex;
          width: 2rem;
          margin-bottom: 0.12rem;
        }
        .lian{
          height: 0.3rem;
          width: 0.16rem;
          position: relative;
          text-align: center;
          .dian{
            width: 0.04rem;
            height: 0.04rem;
            border-radius: 0.04rem;
            background: white;
            position: absolute;
            z-index: 3;
            top: 50%;
            left: 50%;
          }
          .dianbig{
            width: 0.08rem;
            height: 0.08rem;
            left: 40%;
            border-radius: 0.08rem;
          }
          .xian{
            height: 50%;
            width: 1px;
            top:0;
            color: rgba(255,255,255,.3);
            position: absolute;
            left: 60%;
            text-align: center;
            background: white;
          }
          .xian:last-of-type{
            height: 143%;
            width: 1px;
            left: 60%;
            top:60%;
            color: rgba(255,255,255,.3);
            position: absolute;
            text-align: center;
            background: white;
          }
        }
        .statusDetail{
          font-size: 0.17rem;
          color: rgba(255,255,255,.6);
        }
        .statusDetails{
          font-size: 0.19rem;
          color: white;
        }
        .statusDetail,.timeDetail{
          margin-left: 0.13rem;
          height: 0.3rem;
          line-height: 0.3rem;
        }
      }
      li:first-of-type{
        .liuTime{
          .lian{
           visibility: hidden
          }
        }
        .status{
          .lian{
            .xian:first-of-type{visibility: hidden}
          }
        }
      }
      li:last-of-type{
        .status{
          .lian{
            .xian:last-of-type{visibility: hidden}
          }
        }
      }
    }
    .IncomeDetail{
      width: 3.5rem;
      background: white;
      margin: 0.17rem auto;
      min-height: 1.34rem;
      border-radius: 0.04rem;
      padding-top: 0.23rem;
      i{
        font-style: normal;
      }
      div{
        margin-bottom: 0.1rem;
        padding:0 0.13rem;
        display: flex;
        justify-content: space-between;
        .info{
          font-size: 0.16rem;
          color: #333;
          font-weight: 600;
        }
        div{
          width: 2rem;
          display: block;
          text-align: right;
          font-size: 0.14rem;
          color: #9a9a9a;
        }
      }
    }
  }
  .kefu{
    bottom: 0.2rem;
  }
</style>



